header {
    background-color: #333; /* Couleur de fond du header */
    color: #fff; /* Couleur du texte */
    padding: 20px; /* Espace intérieur du header */
    display: flex; /* Utilisation de Flexbox pour aligner les éléments */
    justify-content: space-between; /* Aligner les éléments sur les extrémités */
    align-items: center; /* Aligner verticalement les éléments */
}
h1{
    font-size: 40px;
}  
/* Style pour le titre du header */
.logo-container h1 {
    margin: 0; /* Supprime les marges par défaut */
}
  
/* Style pour la navigation dans le header */
nav ul {
    list-style: none; /* Supprime les puces des listes */
    padding: 0; /* Supprime les marges intérieures des listes */
    display: flex; /* Utilisation de Flexbox pour aligner les éléments */
}

/* Style pour les éléments de la navigation */
nav ul li {
    margin-right: 20px; /* Marge entre les éléments */
}
  
/* Style pour les liens dans la navigation */
nav ul li a {
    color: #fff; /* Couleur des liens */
    text-decoration: none; /* Supprime la soulignement par défaut des liens */
    font-size: 25px;
}
  
/* Style pour les liens dans la navigation au survol */
nav ul li a:hover {
    text-decoration: underline; /* Soulignement au survol */
}