.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}
footer {
  background-color: #333;
  color: #fff;
  padding: 10px 0;
  text-align: center;
}
section {
  margin-bottom: 20px;
}

button {
  background-color: #007bff;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

.page{
  margin-left:2%;
}

.image-container {
  width: 50%; /* Prend 50% de la largeur de l'écran */
  max-width: 720px; /* Limite la largeur de l'image à 720px */
}

.image-container img {
  width: 100%; /* Remplit la largeur du conteneur */
  height: auto; /* Garde le ratio hauteur/largeur d'origine */
  display: block; /* Évite tout espace indésirable sous l'image */
  margin: auto; /* Centre l'image horizontalement */
}